@import 'variables';

.form-dropdown {
  margin-bottom: 0.6rem;

  &__wrapper {
    width: 100%;
    font-size: 1.8rem; }

  &__select {
    box-sizing: border-box;
    color: var(--inputColor);
    border-radius: 6px;
    border: 1px solid var(--inputBorder);
    background-color: var(--inputBg);

    &:focus {
      outline: none; } }

  &__label {
    padding-right: 0.4rem; }

  &__error {
    lost-row: 1;
    color: $red;
    font-size: 1rem; } }
